import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { useQuery } from 'react-query';
import { 
  MapPin, 
  Star, 
  ArrowRight, 
  Users, 
  Heart
} from 'lucide-react';
import { placesAPI } from '../services/api';
import PlaceCard from '../components/places/PlaceCard';
import SearchModal from '../components/search/SearchModal';

const HomePage = () => {
  const [isSearchOpen, setIsSearchOpen] = useState(false);

  // Fetch featured places
  const { data: featuredPlaces = [] } = useQuery(
    'featuredPlaces',
    () => placesAPI.getPlaces({ featured: true, limit: 6 })
  );

  // Fetch top places by region
  const { data: topPlaces = [] } = useQuery(
    'topPlaces',
    () => placesAPI.getTopPlaces('Dakar')
  );

  const features = [
    {
      icon: MapPin,
      title: 'Discover Amazing Places',
      description: 'Find the best restaurants, parks, museums, and more across Senegal.'
    },
    {
      icon: Users,
      title: 'Join Social Hangouts',
      description: 'Connect with locals and travelers through organized meetups and events.'
    },
    {
      icon: Star,
      title: 'Read Authentic Reviews',
      description: 'Get real insights from our community of place explorers.'
    },
    {
      icon: Heart,
      title: 'Save Your Favorites',
      description: 'Build your personal collection of favorite places and share with friends.'
    }
  ];

  const stats = [
    { number: '500+', label: 'Places Discovered' },
    { number: '10K+', label: 'Happy Users' },
    { number: '50+', label: 'Cities Covered' },
    { number: '1000+', label: 'Reviews Posted' }
  ];

  const placeTypes = [
    { type: 'restaurant', name: 'Restaurants', icon: '', color: 'bg-red-500' },
    { type: 'park', name: 'Parks', icon: '', color: 'bg-green-500' },
    { type: 'museum', name: 'Museums', icon: '', color: 'bg-blue-500' },
    { type: 'shopping_center', name: 'Shopping', icon: '', color: 'bg-purple-500' },
    { type: 'hotel', name: 'Hotels', icon: '', color: 'bg-yellow-500' },
    { type: 'cafe', name: 'Cafes', icon: '', color: 'bg-orange-500' }
  ];

  return (
    <div className="min-h-screen">
      {/* Hero Section */}
      <section className="relative bg-gradient-to-br from-primary-500 via-primary-600 to-accent-600 text-white overflow-hidden">
        <div className="absolute inset-0 bg-black opacity-20"></div>
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-24 lg:py-32">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">
            <motion.div
              initial={{ opacity: 0, x: -50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.8 }}
            >
              <h1 className="text-4xl lg:text-6xl font-bold mb-6 leading-tight">
                Discover Amazing
                <span className="block text-accent-300">Places in Senegal</span>
              </h1>
              <p className="text-xl lg:text-2xl mb-8 text-gray-100">
                Your ultimate guide to the best restaurants, parks, museums, and hidden gems across Senegal.
              </p>
              <div className="flex flex-col sm:flex-row gap-4">
                <motion.button
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => setIsSearchOpen(true)}
                  className="bg-white text-primary-600 font-semibold py-4 px-8 rounded-lg hover:bg-gray-100 transition-colors duration-200 flex items-center justify-center space-x-2"
                >
                  <MapPin size={20} />
                  <span>Explore Places</span>
                </motion.button>
                <Link
                  to="/places"
                  className="border-2 border-white text-white font-semibold py-4 px-8 rounded-lg hover:bg-white hover:text-primary-600 transition-colors duration-200 flex items-center justify-center space-x-2"
                >
                  <span>View All Places</span>
                  <ArrowRight size={20} />
                </Link>
              </div>
            </motion.div>
            <motion.div
              initial={{ opacity: 0, x: 50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.8, delay: 0.2 }}
              className="relative"
            >
              <div className="bg-white/10 backdrop-blur-sm rounded-2xl p-8 border border-white/20">
                <div className="grid grid-cols-2 gap-4">
                  {stats.map((stat, index) => (
                    <motion.div
                      key={stat.label}
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ duration: 0.5, delay: 0.3 + index * 0.1 }}
                      className="text-center"
                    >
                      <div className="text-3xl font-bold text-accent-300">{stat.number}</div>
                      <div className="text-sm text-gray-200">{stat.label}</div>
                    </motion.div>
                  ))}
                </div>
              </div>
            </motion.div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section className="py-20 bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
            className="text-center mb-16"
          >
            <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 mb-4">
              Why Choose Foy Lekke?
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              We make it easy to discover and explore the best places Senegal has to offer
            </p>
          </motion.div>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {features.map((feature, index) => {
              const Icon = feature.icon;
              return (
                <motion.div
                  key={feature.title}
                  initial={{ opacity: 0, y: 30 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6, delay: index * 0.1 }}
                  viewport={{ once: true }}
                  className="bg-white rounded-xl p-6 shadow-lg hover:shadow-xl transition-shadow duration-200"
                >
                  <div className="w-12 h-12 bg-primary-100 rounded-lg flex items-center justify-center mb-4">
                    <Icon className="text-primary-600" size={24} />
                  </div>
                  <h3 className="text-xl font-semibold text-gray-900 mb-2">
                    {feature.title}
                  </h3>
                  <p className="text-gray-600">
                    {feature.description}
                  </p>
                </motion.div>
              );
            })}
          </div>
        </div>
      </section>

      {/* Place Types Section */}
      <section className="py-20 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
            className="text-center mb-16"
          >
            <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 mb-4">
              Explore by Category
            </h2>
            <p className="text-xl text-gray-600 max-w-3xl mx-auto">
              Discover different types of places that match your interests
            </p>
          </motion.div>

          <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-6">
            {placeTypes.map((placeType, index) => (
              <motion.div
                key={placeType.type}
                initial={{ opacity: 0, scale: 0.8 }}
                whileInView={{ opacity: 1, scale: 1 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
                viewport={{ once: true }}
                whileHover={{ scale: 1.05 }}
              >
                <Link
                  to={`/places?type=${placeType.type}`}
                  className="block bg-white rounded-xl p-6 shadow-lg hover:shadow-xl transition-all duration-200 border border-gray-100"
                >
                  <div className={`w-12 h-12 ${placeType.color} rounded-lg flex items-center justify-center mb-4 mx-auto`}>
                    <span className="text-2xl">{placeType.icon}</span>
                  </div>
                  <h3 className="text-center font-semibold text-gray-900">
                    {placeType.name}
                  </h3>
                </Link>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Featured Places Section */}
      {featuredPlaces.length > 0 && (
        <section className="py-20 bg-gray-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6 }}
              viewport={{ once: true }}
              className="flex justify-between items-center mb-12"
            >
              <div>
                <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 mb-2">
                  Featured Places
                </h2>
                <p className="text-xl text-gray-600">
                  Handpicked destinations you'll love
                </p>
              </div>
              <Link
                to="/places"
                className="btn-primary flex items-center space-x-2"
              >
                <span>View All</span>
                <ArrowRight size={20} />
              </Link>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {featuredPlaces.map((place, index) => (
                <motion.div
                  key={place._id}
                  initial={{ opacity: 0, y: 30 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6, delay: index * 0.1 }}
                  viewport={{ once: true }}
                >
                  <PlaceCard place={place} />
                </motion.div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* Top Places Section */}
      {topPlaces.length > 0 && (
        <section className="py-20 bg-white">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6 }}
              viewport={{ once: true }}
              className="text-center mb-16"
            >
              <h2 className="text-3xl lg:text-4xl font-bold text-gray-900 mb-4">
                Top Places in Dakar
              </h2>
              <p className="text-xl text-gray-600 max-w-3xl mx-auto">
                The most popular and highly-rated places in the capital
              </p>
            </motion.div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {topPlaces.slice(0, 6).map((place, index) => (
                <motion.div
                  key={place._id}
                  initial={{ opacity: 0, y: 30 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6, delay: index * 0.1 }}
                  viewport={{ once: true }}
                >
                  <PlaceCard place={place} />
                </motion.div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* CTA Section */}
      <section className="py-20 bg-gradient-to-r from-primary-500 to-accent-600 text-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <h2 className="text-3xl lg:text-4xl font-bold mb-6">
              Ready to Start Exploring?
            </h2>
            <p className="text-xl mb-8 text-gray-100 max-w-3xl mx-auto">
              Join thousands of people discovering amazing places in Senegal. 
              Create an account to save your favorites and join social hangouts.
            </p>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Link
                to="/register"
                className="bg-white text-primary-600 font-semibold py-4 px-8 rounded-lg hover:bg-gray-100 transition-colors duration-200"
              >
                Get Started
              </Link>
              <Link
                to="/places"
                className="border-2 border-white text-white font-semibold py-4 px-8 rounded-lg hover:bg-white hover:text-primary-600 transition-colors duration-200"
              >
                Browse Places
              </Link>
            </div>
          </motion.div>
        </div>
      </section>

      {/* Search Modal */}
      <SearchModal isOpen={isSearchOpen} onClose={() => setIsSearchOpen(false)} />
    </div>
  );
};

export default HomePage; 