import React from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { 
  MapPin, 
  Clock, 
  Phone, 
  Heart,
  TrendingUp,
  Globe
} from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import StarRating from '../ui/StarRating';

const PlaceCard = ({ place }) => {
  const { user, toggleFavorite } = useAuth();
  const isFavorite = user?.favorites?.includes(place._id);

  const placeTypeIcons = {
    restaurant: '',
    park: '',
    museum: '',
    shopping_center: '',
    hotel: '',
    cafe: '',
    bar: '',
    entertainment: '',
    cultural: '',
    sports: '',
    other: ''
  };

  const getTypeColor = (type) => {
    const colors = {
      restaurant: 'bg-red-100 text-red-800',
      park: 'bg-green-100 text-green-800',
      museum: 'bg-blue-100 text-blue-800',
      shopping_center: 'bg-purple-100 text-purple-800',
      hotel: 'bg-yellow-100 text-yellow-800',
      cafe: 'bg-orange-100 text-orange-800',
      bar: 'bg-pink-100 text-pink-800',
      entertainment: 'bg-indigo-100 text-indigo-800',
      cultural: 'bg-teal-100 text-teal-800',
      sports: 'bg-emerald-100 text-emerald-800',
      other: 'bg-gray-100 text-gray-800'
    };
    return colors[type] || colors.other;
  };

  const handleFavoriteClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    if (user) {
      toggleFavorite(place._id);
    }
  };

  const formatRating = (rating) => {
    if (!rating) return 'No ratings';
    return `${rating.toFixed(1)} (${place.reviewCount || 0} reviews)`;
  };

  const formatPrice = (priceLevel) => {
    if (!priceLevel) return 'Price not available';
    return ''.repeat(priceLevel);
  };

  return (
    <motion.div
      whileHover={{ y: -5 }}
      className="group"
    >
      <Link to={`/places/${place._id}`} className="block">
        <div className="bg-white rounded-xl shadow-lg hover:shadow-xl transition-all duration-200 overflow-hidden border border-gray-100">
          {/* Image Section */}
          <div className="relative h-48 overflow-hidden">
            {place.photos && place.photos.length > 0 ? (
              <img
                src={place.photos[0]}
                alt={place.name}
                className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
              />
            ) : (
              <div className="w-full h-full bg-gradient-to-br from-primary-100 to-accent-100 flex items-center justify-center">
                <span className="text-4xl">{placeTypeIcons[place.type] || ''}</span>
              </div>
            )}
            
            {/* Favorite Button */}
            {user && (
              <button
                onClick={handleFavoriteClick}
                className="absolute top-3 right-3 p-2 bg-white/90 backdrop-blur-sm rounded-full shadow-lg hover:bg-white transition-colors duration-200"
              >
                <Heart
                  size={20}
                  className={`${
                    isFavorite ? 'fill-red-500 text-red-500' : 'text-gray-600'
                  } hover:text-red-500 transition-colors duration-200`}
                />
              </button>
            )}

            {/* Type Badge */}
            <div className="absolute top-3 left-3">
              <span className={`px-2 py-1 rounded-full text-xs font-medium ${getTypeColor(place.type)}`}>
                {place.type.replace('_', ' ').toUpperCase()}
              </span>
            </div>

            {/* Featured Badge */}
            {place.featured && (
              <div className="absolute bottom-3 left-3">
                <span className="px-2 py-1 bg-gradient-to-r from-primary-500 to-accent-500 text-white rounded-full text-xs font-medium flex items-center space-x-1">
                  <TrendingUp size={12} />
                  <span>FEATURED</span>
                </span>
              </div>
            )}
          </div>

          {/* Content Section */}
          <div className="p-4">
            {/* Title and Rating */}
            <div className="mb-3">
              <h3 className="font-bold text-lg text-gray-900 mb-1 group-hover:text-primary-600 transition-colors duration-200">
                {place.name}
              </h3>
              <div className="flex items-center space-x-2 text-sm text-gray-600">
                <StarRating 
                  rating={place.rating || 0} 
                  size={14} 
                  showValue={false}
                />
                <span>{formatRating(place.rating)}</span>
                {place.priceLevel && (
                  <>
                    <span></span>
                    <span>{formatPrice(place.priceLevel)}</span>
                  </>
                )}
              </div>
            </div>

            {/* Location */}
            <div className="flex items-start space-x-2 mb-3">
              <MapPin size={16} className="text-gray-400 mt-0.5 flex-shrink-0" />
              <p className="text-sm text-gray-600 line-clamp-2">
                {place.address}
              </p>
            </div>

            {/* Additional Info */}
            <div className="space-y-2">
              {/* Hours */}
              {place.openingHours && (
                <div className="flex items-center space-x-2 text-sm text-gray-600">
                  <Clock size={14} className="text-gray-400" />
                  <span>
                    {place.openingHours.openNow ? 'Open now' : 'Closed'}
                  </span>
                </div>
              )}

              {/* Phone */}
              {place.phone && (
                <div className="flex items-center space-x-2 text-sm text-gray-600">
                  <Phone size={14} className="text-gray-400" />
                  <span>{place.phone}</span>
                </div>
              )}

              {/* Website */}
              {place.website && (
                <div className="flex items-center space-x-2 text-sm text-gray-600">
                  <Globe size={14} className="text-gray-400" />
                  <span className="truncate">Website available</span>
                </div>
              )}
            </div>

            {/* Tags */}
            {place.tags && place.tags.length > 0 && (
              <div className="mt-3 flex flex-wrap gap-1">
                {place.tags.slice(0, 3).map((tag, index) => (
                  <span
                    key={index}
                    className="px-2 py-1 bg-gray-100 text-gray-600 rounded-full text-xs"
                  >
                    {tag}
                  </span>
                ))}
                {place.tags.length > 3 && (
                  <span className="px-2 py-1 bg-gray-100 text-gray-600 rounded-full text-xs">
                    +{place.tags.length - 3} more
                  </span>
                )}
              </div>
            )}

            {/* Restaurant-specific info */}
            {place.type === 'restaurant' && place.restaurantData && (
              <div className="mt-3 pt-3 border-t border-gray-100">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-600">
                    Cuisine: {place.restaurantData.cuisineType || 'Various'}
                  </span>
                  {place.restaurantData.deliveryAvailable && (
                    <span className="text-green-600 font-medium">Delivery</span>
                  )}
                </div>
              </div>
            )}

            {/* Park-specific info */}
            {place.type === 'park' && place.parkData && (
              <div className="mt-3 pt-3 border-t border-gray-100">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-600">
                    Size: {place.parkData.size || 'N/A'}
                  </span>
                  {place.parkData.hasPlayground && (
                    <span className="text-blue-600 font-medium">Playground</span>
                  )}
                </div>
              </div>
            )}

            {/* Museum-specific info */}
            {place.type === 'museum' && place.museumData && (
              <div className="mt-3 pt-3 border-t border-gray-100">
                <div className="flex items-center justify-between text-sm">
                  <span className="text-gray-600">
                    Type: {place.museumData.museumType || 'General'}
                  </span>
                  {place.museumData.hasGuidedTours && (
                    <span className="text-purple-600 font-medium">Guided Tours</span>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
      </Link>
    </motion.div>
  );
};

export default PlaceCard; 