import axios from 'axios';

const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL || 'http://localhost:9999',
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor to add auth token
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Response interceptor to handle errors
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

// Places API
export const placesAPI = {
  // Get all places with filters
  getPlaces: (params = {}) => api.get('/api/places', { params }),
  
  // Get place types
  getPlaceTypes: () => api.get('/api/places/types'),
  
  // Get single place
  getPlace: (id) => api.get(`/api/places/${id}`),
  
  // Create new place
  createPlace: (data) => api.post('/api/places', data),
  
  // Update place
  updatePlace: (id, data) => api.put(`/api/places/${id}`, data),
  
  // Delete place
  deletePlace: (id) => api.delete(`/api/places/${id}`),
  
  // Get top places by region
  getTopPlaces: (region, type) => api.get(`/api/places/top/${region}`, { params: { type } }),
  
  // Sync places from Google
  syncPlaces: (region, type, limit) => api.post(`/api/places/sync/${region}`, {}, { params: { type, limit } }),
};

